<template>
  <div class="Scene" ref="scene"></div>
</template>

<script>
  import Animation from '../common/Animation.class';

  let previousStep = 0;

  export default {
    name: 'threeDebug',
    props: ['isActive', 'isDebug', 'step', 'isStarted', 'updateCb'],
    data() {
      return {
        model: 'wpt',
      };
    },
    watch: {
      step(val) {
        this.animation.setStep(val, true);

        if (Math.abs(val - previousStep) > 1) {
          if (val < 3 && this.model === 'wroclavia') {
            this.animation.switchBuildings('wpt');
            this.model = 'wpt';
          }
          if (val >= 3 && this.model === 'wpt') {
            this.animation.switchBuildings('wroclavia');
            this.model = 'wroclavia';
          }
        } else if (val === 2 && previousStep === 3) {
          this.animation.switchBuildings('wpt');
          this.model = 'wpt';
        } else if (val === 3 && previousStep === 2) {
          this.animation.switchBuildings('wroclavia');
          this.animation.createTrails();
          this.model = 'wroclavia';
        } else if (val === 4 && previousStep === 3) {
          // this.animation.removeTrails();
        } else if (val === 3 && previousStep === 4) {
          this.animation.createTrails();
        }

        previousStep = val;
      },
      isActive(val) {
        this.animation.setActive(val);
      },
    },
    mounted() {
      const options = {
        active: this.isActive,
        debug: this.isDebug,
        updateCb: this.updateCb,
        container: this.$refs.scene,
        exposeScene: false,
        model: this.model, // this.isDebug ? 'wroclavia' : 'wpt', // TODO: debug set initial model
        step: 0,
      };

      this.animation = new Animation(options);
      this.animation.init(p => {
          this.$emit('progress', p);
        })
        .then(() => {
          this.$emit('loaded');
          this.animation.startTrucksAnimation('wpt');
        });
    },
  };

</script>

<style scoped>
  .Scene {
    flex: 1;
    z-index: 10;
    position: absolute;
    width: 100vw;
    height: 100vh;
    align-content: center;
    justify-content: center;
    opacity: 1;
  }

  .Scene::after {
    content: "";
    background-size: cover;
    opacity: 0.25;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: 0;
  }

</style>



