<template>
  <nav :class="{ 'is-active': isActive }">
    <img src="../assets/pgssoftware-logo-inverted.svg" @click.prevent="onClick(0)"/>
    <ul>
      <li>
        <a href='#' @click.prevent="onClick(7)">kontakt</a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ['isActive', 'onClick'],
  name: 'nav',
};
</script>

<style lang="scss" scoped>
@import 'base';

nav {
  // opacity: 0.8;
  padding: 0 4.5%;
  box-sizing: border-box;
  position: fixed;
  height: 70px;
  display: flex;
  z-index: 1600;
  flex-direction: row;
  justify-content: space-between;
  transition: all 0.2s ease-in-out;
  width: 100%;
  margin-top: 5px;
  font-size: 16px;

  @include for-size(phone-only) {
    font-size: 10px;
  }

  @include for-size(big-desktop-up) {
    padding: 0;
    width: $maxWidth;
    left:calc(50% - #{$maxWidth / 2});
  }

  img {
    height: 100%;
    cursor: pointer;
  }

  ul {
    display: flex;
    align-self: flex-end;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
    align-items: center;
  }

  ul li {
    list-style: none;
    text-align: center;
    transition: line-height 0.2s ease-in-out;
  }

  a:not(:last-child) {
    margin-right: 20px;
  }

  a, a:active, a:visited {
    text-transform: uppercase;
    color: $color-white;
    text-decoration: none;
  }

  a:hover {
   color: $color-persimmon;
   font-weight: 700; 
  }

}

nav:hover {
  opacity: 1;
}

nav.is-active {
  height: 100px;
  opacity: 1;
  font-size: 18px;
  
  @include for-size(phone-only) {
    font-size: 12px;
  }

}

</style>




