import * as THREE from 'three';

import { isFunction, throttle } from 'lodash';

import { TweenMax, Power0, Power4 } from 'gsap';

import {
  // deg2rad,
  normalizeAngle,
  concat,
  applyTransform,
  createPath,
  getPointInBetweenByPerc,
  v3,
  v3dir,
  isMobile,
  pointSpline,
  getRandomItem,
} from './utils';

import Trail from './Trail.class';

// INTERNAL VARIABLES
import {
  CAMERA_SPEED,
  CAMERA_SPEED_MOBILE,
  IMPORT_SCALE,
  MATERIALS,
  CONSTRAINTS,
  PALLETE,
  PATHS,
  POSITIONS,
} from './Animation.const';

// MODELS PARTIALS SETTINGS
import {
  ELEMENTS_WPT,
  ELEMENTS_WROCLAVIA,
  PATHS_WROCLAVIA,
  PARTIALS_WPT,
  PARTIALS_WROCLAVIA,
} from './Animation.models';

import OrbitControls from '../lib/OrbitControls';

const IS_MOBILE = isMobile();

// CLASS IMPLEMENTATION
class Animation {
  constructor(options) {
    this.scene = new THREE.Scene();
    this.clock = new THREE.Clock();

    this.models = {};
    this.currentModel = options.model || 'wpt';
    this.trails = [];
    this.theta = 0;

    this.currentStep = options.step || 0; // 0 -> 0, 1 -> 1 ...
    this.container = options.container;
    this.updateCb = options.updateCb;
    this.flags = {
      isActive: options.active,
      isDebug: options.debug,
      exposeScene: options.exposeScene,
      isInitialized: false,
      isLoaded: false,
      isTransition: false,
    };

    if (this.flags.exposeScene) {
      window.THREE = THREE;
      window.scene = this.scene;
    }
  }

  // SCENE OBJECTS
  addCamera() {
    const camera = new THREE.PerspectiveCamera(20, this.WIDTH / this.HEIGHT, 1, 100000);
    camera.lookAt(this.scene.position);

    this.camera = camera;
  }

  addHelpers() {
    const axisHelper = new THREE.AxisHelper(10);
    const gridHelper = new THREE.GridHelper(1000, 1000);

    this.scene.add(axisHelper);
    this.scene.add(gridHelper);
  }

  loadModels(cb) {
    const loadingManager = new THREE.LoadingManager();
    const loader = new THREE.JSONLoader(loadingManager);

    this.setCamera(v3(0, 0, 100), v3(0, 0, 0));
    this.animate();

    return new Promise(resolve => {
      const wptModel = new THREE.Object3D();
      const wroclaviaModel = new THREE.Object3D();
      const busModel = {};
      const truckModel = {
        models: [],
        splines: {
          wpt: [],
          wroclavia: [],
        },
      };

      PARTIALS_WPT.filter(partial => (IS_MOBILE ? !partial.mobileHidden : true)).forEach(partial => {
        loader.load(`${PATHS.models}${partial.model}`, object => {
          wptModel.add(createModel(object, partial));
        });
      });

      PARTIALS_WROCLAVIA.filter(partial => (IS_MOBILE ? !partial.mobileHidden : true)).forEach(partial => {
        loader.load(`${PATHS.models}${partial.model}`, object => {
          wroclaviaModel.add(createModel(object, partial));
        });
      });

      /* --- ANIMATION ELEMENTS --- */

      // 1. Wpt trucks
      loader.load(`${PATHS.models}${ELEMENTS_WPT.truck.model}`, object => {
        for (let i = 0; i < ELEMENTS_WPT.truck.count; i++) {
          const model = createModel(object, ELEMENTS_WPT.truck);
          model.position.x = i / 2;
          truckModel.models.push(model);
        }
      });

      // 2. Wpt truck paths
      [
        `${PATHS.models}${ELEMENTS_WPT.path1.model}`,
        `${PATHS.models}${ELEMENTS_WPT.path2.model}`,
        `${PATHS.models}${ELEMENTS_WPT.path3.model}`,
      ].forEach(path => {
        loader.load(path, object => {
          const pathSpline = createPath(object, IMPORT_SCALE, ELEMENTS_WPT.path1.position);
          truckModel.splines.wpt.push(pathSpline.spline);
        });
      });

      // 3. Wroclavia bus
      loader.load(`${PATHS.models}${ELEMENTS_WROCLAVIA.bus.model}`, object => {
        busModel.model = new THREE.Object3D();
        const model = createModel(object, ELEMENTS_WROCLAVIA.bus);
        applyTransform(model);
        busModel.model.add(model);
      });

      // 4. Wroclavia bus path
      loader.load(`${PATHS.models}${ELEMENTS_WROCLAVIA.busPath.model}`, object => {
        const path = createPath(object, IMPORT_SCALE, ELEMENTS_WROCLAVIA.busPath.position);
        busModel.spline = path.spline;
        busModel.line = path.line;
      });

      // 5. Wroclavia truck paths
      [
        `${PATHS.models}${ELEMENTS_WROCLAVIA.path1.model}`,
        `${PATHS.models}${ELEMENTS_WROCLAVIA.path2.model}`,
        `${PATHS.models}${ELEMENTS_WROCLAVIA.path3.model}`,
      ].forEach(path => {
        loader.load(path, object => {
          const pathSpline = createPath(object, IMPORT_SCALE, ELEMENTS_WPT.path1.position);
          truckModel.splines.wroclavia.push(pathSpline.spline);
        });
      });

      /* --- LOADING HANDLER --- */
      loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => {
        if (isFunction(cb)) {
          cb(itemsLoaded / itemsTotal);
        }
      };

      loadingManager.onLoad = () => {
        this.models.wpt = wptModel;
        this.models.wroclavia = wroclaviaModel;
        this.models.bus = busModel;
        this.models.truck = truckModel;

        this.scene.add(this.models.wpt);
        this.scene.add(this.models.wroclavia);

        // mooving out not current model
        const modelOut = this.currentModel === 'wpt' ? 'wroclavia' : 'wpt';
        this.models[modelOut].position.set(0, 1000, 0);

        this.flags.isLoaded = true;
        resolve();
      };
    });
  }

  // EVENTS, HANDLERS

  initRenderer() {
    const renderer = new THREE.WebGLRenderer({
      alpha: true,
      antialias: true,
    });

    renderer.shadowMap.soft = false;
    renderer.shadowMap.enabled = true;
    renderer.setSize(this.WIDTH, this.HEIGHT);

    this.container.appendChild(renderer.domElement);
    this.renderer = renderer;
  }

  initEvents() {
    if (this.flags.isDebug) {
      document.addEventListener('keydown', this.onKeyDown.bind(this));
    } else if (IS_MOBILE) {
      window.addEventListener('deviceorientation', this.onDeviceOrientation.bind(this));
    } else {
      document.addEventListener('mousemove', throttle(this.onDocumentMouseMove.bind(this), 50), false);
    }

    window.addEventListener('resize', this.updateSize.bind(this), false);
  }

  updateSize() {
    this.WIDTH = this.container.offsetWidth;
    this.HEIGHT = this.container.offsetHeight;

    if (this.flags.isInitialized) {
      this.camera.aspect = this.WIDTH / this.HEIGHT;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(this.WIDTH, this.HEIGHT);
      this.render();
    }
  }

  onDocumentMouseMove(event) {
    if (!this.flags.isTransition) {
      this.moveProgress = event.clientX / this.WIDTH;
      this.moveOffset = (-1 * (event.clientY - (this.HEIGHT / 2))) / (this.HEIGHT / 2);

      const cameraStart = POSITIONS[this.currentStep][1];
      const cameraEnd = POSITIONS[this.currentStep][2];

      this.cameraMove = getPointInBetweenByPerc(cameraStart, cameraEnd, this.moveProgress);
      this.cameraMove.y = Math.max(this.cameraMove.y - (this.moveOffset * CONSTRAINTS.moveY), CONSTRAINTS.minY);

      this.cursor = {
        x: event.clientX,
        y: event.clientY,
        xProgress: event.clientX / this.WIDTH,
        yProgress: event.clientY / this.Height,
      };
    }
  }

  onDeviceOrientation(event) {
    const isPortait = window.innerWidth < window.innerHeight;
    const beta = isPortait ? event.beta : event.gamma;
    const gamma = isPortait ? event.gamma : event.beta;

    const vector = {
      x: normalizeAngle(gamma, 0, 50),
      y: normalizeAngle(beta, 30, 30),
    };

    const cameraStart = POSITIONS[this.currentStep][1];
    const cameraEnd = POSITIONS[this.currentStep][2];
    const mid = getPointInBetweenByPerc(cameraStart, cameraEnd, 0.5);

    if (!this.flags.isTransition) {
      this.thetaTarget = vector.x * 90 * (Math.PI / 180);
      this.cameraMove.y = Math.max(mid.y - (vector.y * 5), CONSTRAINTS.minY);
    }
  }

  onKeyDown(e) {
    switch (e.keyCode) {
      case 83: {
        /* eslint-disable */
        console.log(
          'camera position:',
          `${this.camera.position.x.toFixed(2)},
           ${this.camera.position.y.toFixed(2)},
           ${this.camera.position.z.toFixed(2)}`,
        );
        /* eslint-enable */

        localStorage.setItem(
          'savedCamera',
          JSON.stringify({
            cameraPosition: this.camera.position,
            targetPosition: this.controls.target,
          }),
        );
        break;
      }
      case 68: {
        const savedCamera = JSON.parse(localStorage.getItem('savedCamera'));
        if (savedCamera) {
          this.camera.position.copy(savedCamera.cameraPosition);
          this.camera.lookAt(savedCamera.targetPosition);
          this.controls.target.copy(savedCamera.targetPosition);
        }
        break;
      }
      default:
        break;
    }
  }

  // INTERNALS
  render() {
    this.renderer.render(this.scene, this.camera);
  }

  rotateCamera(alfa) {
    // const cameraStart = POSITIONS[this.currentStep][1];
    // const cameraEnd = POSITIONS[this.currentStep][2];
    // const cameraMiddle = getPointInBetweenByPerc(cameraStart, cameraEnd, 0.5);
    const cameraTarget = POSITIONS[this.currentStep][0];

    const quaternion = new THREE.Quaternion();
    quaternion.setFromAxisAngle(new THREE.Vector3(0, 1, 0).normalize(), alfa);
    this.camera.position.applyQuaternion(quaternion);
    this.camera.lookAt(cameraTarget);
  }

  animate() {
    if (this.flags.isActive) {
      if (this.flags.isLoaded && this.flags.isInitialized) {
        if (this.cameraMove && !this.flags.isDebug) {
          const dir = v3dir(this.cameraMove, this.camera.position);
          const cameraSpeed = IS_MOBILE && !this.flags.isTransition ? CAMERA_SPEED * CAMERA_SPEED_MOBILE : CAMERA_SPEED;

          this.camera.position.x += dir.x * cameraSpeed;
          this.camera.position.y += dir.y * cameraSpeed;
          this.camera.position.z += dir.z * cameraSpeed;

          this.camera.lookAt(this.cameraTarget);

          /* if (dir.x < 1 && dir.y < 1 && dir.x < 1) {
            this.cameraMove = null;
          } */
        }

        if (IS_MOBILE && this.thetaTarget && !this.flags.isTransition) {
          this.theta += (this.thetaTarget - this.theta) / 1000;
          this.rotateCamera(this.theta);
        }

        this.trails.forEach(trail => trail.run());

        if (this.flags.isDebug) {
          this.controls.update();
          this.camera.lookAt(POSITIONS[this.currentStep][0]);
        }

        if (this.updateCb) {
          this.updateCb(this.camera, this.renderer, this.clock.getElapsedTime());
        }
      }

      if (this.flags.debug) {
        this.controls.update();
      }

      this.render();
    }

    requestAnimationFrame(this.animate.bind(this));
  }

  setSceneModel() {
    this.cameraTarget = POSITIONS[this.currentStep][0];
  }

  setCamera(position, target) {
    this.camera.position.copy(position);
    this.camera.lookAt(target);
  }

  setCameraByStep(step, addMovement) {
    const targetStep = step !== undefined ? step : this.currentStep;
    const progress = this.cursor ? this.cursor.xProgress : 0.5;

    this.cameraTarget = POSITIONS[targetStep][0];
    this.setCamera(POSITIONS[targetStep][1], POSITIONS[targetStep][0]);

    if (addMovement) {
      this.cameraMove = getPointInBetweenByPerc(POSITIONS[targetStep][1], POSITIONS[targetStep][2], progress);
    }
  }

  setActive(val) {
    this.flags.isActive = val;
  }

  setStep(val, force) {
    const progress = this.cursor ? this.cursor.xProgress : 0.5;

    this.currentStep = val;
    if (force) {
      this.cameraMove = getPointInBetweenByPerc(POSITIONS[val][1], POSITIONS[val][2], progress);
    }
  }

  startTrucksAnimation(model = 'wpt') {
    if (!this.flags.isDebug) {
      for (let i = 0; i < ELEMENTS_WPT.truck.count; i++) {
        const truck = this.models.truck.models[i];

        const truckProperties = {
          p: 0,
        };

        const splines = model === 'wpt' ? this.models.truck.splines.wpt : this.models.truck.splines.wroclavia;
        const spline = splines[Math.floor(Math.random() * splines.length)];

        this.trucksAnimation = TweenMax.to(truckProperties, model === 'wpt' ? 20 : 10, {
          p: 1,
          repeat: -1,
          ease: Power0.easeNone,
          delay: (i * 0.5) + (Math.random() * 0.5),
          onStart: () => {
            if (!this.scene.getObjectById(truck.id)) {
              this.scene.add(truck);
            }
          },
          onUpdate: () => {
            const point = pointSpline(truckProperties.p, spline);
            truck.position.copy(point.position);
            truck.quaternion.setFromAxisAngle(point.axis, point.radians);
          },
          onComplete: () => {
            this.scene.remove(truck);
          },
        });
      }
    }
  }

  moveBus(pos = 0.5) {
    // pos : 0.0 - 1.0
    if (!this.scene.getObjectById(this.models.bus.model.id)) {
      this.scene.add(this.models.bus.model);
    }

    const point = pointSpline(pos, this.models.bus.spline);

    this.models.bus.model.position.copy(point.position);
    this.models.bus.model.quaternion.setFromAxisAngle(point.axis, point.radians);
  }

  startBusAnimation() {
    const bus = {
      progress: 0.2,
    };

    TweenMax.to(bus, 30, {
      progress: 1,
      repeat: 10,
      ease: Power0.easeNone,
      onStart: () => {
        if (!this.scene.getObjectById(this.models.bus.model.id)) {
          this.scene.add(this.models.bus.model);
        }
      },
      onUpdate: () => {
        const point = pointSpline(bus.progress, this.models.bus.spline);
        this.models.bus.model.position.copy(point.position);
        this.models.bus.model.quaternion.setFromAxisAngle(point.axis, point.radians);
      },
      onComplete: () => {
        this.scene.remove(this.models.bus.model);
      },
    });
  }

  switchBuildings(switchTo) {
    const self = this;

    const modelA = switchTo === 'wroclavia' ? self.models.wpt : self.models.wroclavia;
    const modelB = switchTo === 'wroclavia' ? self.models.wroclavia : self.models.wpt;

    const paramsOut = {
      p: 0,
      y: 0,
      s: 1,
    };

    const paramsIn = {
      p: 0,
      y: 1 * (Math.PI * 2),
      s: 10,
      h: -100,
    };
    let isSwitched = false;

    this.flags.isTransition = true;
    this.cameraMove = v3(0, 120, 0);

    TweenMax.killAll();

    for (let i = 0; i < ELEMENTS_WPT.truck.count; i++) {
      const truck = self.models.truck.models[i];
      self.scene.remove(truck);
    }

    if (!IS_MOBILE) {
      TweenMax.to(paramsOut, 1.5, {
        p: 1,
        y: -1 * (Math.PI * 2) * 1,
        s: 5,
        ease: Power4.easeIn,
        onStart() {
          modelB.position.set(0, 1200, 0);
          if (switchTo === 'wpt') {
            self.removeTrails();
          }
        },
        onUpdate() {
          modelA.rotation.y = paramsOut.y;
          // modelA.scale.set(paramsOut.s, paramsOut.s, paramsOut.s);
          if (paramsOut.p > 0.5 && !isSwitched) {
            self.startTrucksAnimation(switchTo);
            TweenMax.to(paramsIn, 1, {
              p: 1,
              y: 0,
              s: 1,
              h: 0,
              ease: Power4.easeOut,
              onStart() {
                modelB.position.set(0, 0, 0);
              },
              onUpdate() {
                modelB.rotation.y = paramsIn.y;
                // modelB.position.set(0, paramsIn.h, 0);
                // modelB.scale.set(paramsIn.s, paramsIn.s, paramsIn.s);
              },
              onComplete() {
                self.flags.isTransition = false;
                self.setStep(self.currentStep, true);
              },
            });

            isSwitched = true;
          }
        },
        onComplete() {
          // self.scene.remove(modelA);
          modelA.position.set(0, 1000, 0);
        },
      });
    } else {
      TweenMax.to(paramsOut, 1.5, {
        p: 1,
        y: -1 * (Math.PI * 2) * 1,
        s: 5,
        ease: Power4.easeIn,
        onStart() {
          modelB.position.set(0, 200, 0);
        },
        onUpdate() {
          modelA.rotation.y = paramsOut.y;
        },
        onComplete() {
          // self.scene.remove(modelA);
          modelA.position.set(0, 1000, 0);
          TweenMax.to(paramsIn, 1, {
            p: 1,
            y: 0,
            s: 1,
            h: 0,
            ease: Power4.easeOut,
            onStart() {
              modelB.position.set(0, 0, 0);
            },
            onUpdate() {
              modelB.rotation.y = paramsIn.y;
              // modelB.position.set(0, paramsIn.h, 0);
              // modelB.scale.set(paramsIn.s, paramsIn.s, paramsIn.s);
            },
            onComplete() {
              self.flags.isTransition = false;
              self.setStep(self.currentStep, true);
              self.startTrucksAnimation(switchTo);
            },
          });
        },
      });
    }
  }

  createTrails() {
    const paths = concat(
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
      PATHS_WROCLAVIA,
    );

    paths.forEach((path, idx) => {
      setTimeout(() => {
        const trail = new Trail(getRandomItem(PALLETE.trails), path, this.camera, this.WIDTH, this.HEIGHT);
        this.trails.push(trail);
        this.scene.add(trail.lineMesh);
      }, (idx * 100) + Math.floor(Math.random() * 50));
    });
  }

  removeTrails() {
    this.trails.forEach(trail => this.scene.remove(trail.lineMesh));
  }

  addDebug() {
    const sphereGeometry = new THREE.OctahedronGeometry(0.2, 3);
    const sphereMaterial = new THREE.MeshBasicMaterial({
      color: 0xffffff,
    });

    const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
    sphere.position.copy(POSITIONS[this.currentStep][0]);

    this.debug = {
      sphere,
    };

    this.scene.add(sphere);
  }

  addGUI() {
    const self = this;
    const gui = new dat.GUI();
    const vars = new Variables();

    const point0Folder = gui.addFolder('Point (0,0)');
    point0Folder
      .add(vars.point0, 'x')
      .step(0.1)
      .onChange(setPoint0);
    point0Folder
      .add(vars.point0, 'y')
      .step(0.1)
      .onChange(setPoint0);
    point0Folder
      .add(vars.point0, 'z')
      .step(0.1)
      .onChange(setPoint0);

    function setPoint0() {
      POSITIONS[self.currentStep][0].copy(vars.point0);
      self.debug.sphere.position.copy(POSITIONS[self.currentStep][0]);
      self.cameraTarget.copy(POSITIONS[self.currentStep][0]);
    }

    function Variables() {
      this.point0 = {
        x: POSITIONS[self.currentStep][0].x,
        y: POSITIONS[self.currentStep][0].y,
        z: POSITIONS[self.currentStep][0].z,
      };

      this.pointA = {
        x: POSITIONS[self.currentStep][1].x,
        y: POSITIONS[self.currentStep][1].y,
        z: POSITIONS[self.currentStep][1].z,
      };

      this.pointB = {
        x: POSITIONS[self.currentStep][2].x,
        y: POSITIONS[self.currentStep][2].y,
        z: POSITIONS[self.currentStep][2].z,
      };
    }
  }

  addControls() {
    this.controls = new OrbitControls(this.camera, this.container);
    this.controls.minDistance = 0;
    this.controls.maxDistance = 400;
    this.controls.target.copy(POSITIONS[this.currentStep][0]);
  }

  // MAIN INIT
  init(cb) {
    return new Promise(resolve => {
      this.updateSize();

      this.addCamera();
      // this.addHelpers();

      this.initRenderer();
      this.render();

      this.loadModels(cb).then(() => {
        this.clock.start();
        this.initEvents();
        this.setCameraByStep(this.currentStep, true);
        this.flags.isInitialized = true;

        this.updateSize();
        resolve();
      });

      if (this.flags.isDebug) {
        /* eslint-disable */
        console.warn('--- DEBUG MODE ---');
        this.addGUI();
        this.addDebug();
        this.addControls();
        /* eslint-disable */
      }
    });
  }
}

function createModel(object, options) {
  const scale = IMPORT_SCALE; // * (options.importScale || 1);
  const group = new THREE.Object3D();

  if (options.edges && !IS_MOBILE) {
    const edgesGeo = new THREE.EdgesGeometry(object);
    const edges = new THREE.LineSegments(edgesGeo, MATERIALS.edgesMat);
    edges.scale.set(scale, scale, scale);
    if (options.position) {
      edges.position.copy(v3(options.position[0], options.position[1], options.position[2]));
    }

    group.add(edges);
  }

  if (options.wireframe) {
    const wireframe = new THREE.Mesh(object, MATERIALS.wireframeMat);
    wireframe.scale.set(scale, scale, scale);
    if (options.position) {
      wireframe.position.copy(v3(options.position[0], options.position[1], options.position[2]));
    }

    group.add(wireframe);
  }

  if (options.faces && !IS_MOBILE) {
    const faces = new THREE.Mesh(object, MATERIALS.facesMat);

    faces.scale.set(scale, scale, scale);
    if (options.position) {
      faces.position.copy(v3(options.position[0], options.position[1], options.position[2]));
    }

    group.add(faces);
  }

  return group;
}

export default Animation;



