<template>
  <div class="chartContainer">
  </div>
</template>

<script>
import moment from 'moment';
import * as d3 from 'd3';
import 'd3-selection-multi';

const POINT_RADIUS = 3;
const width = 1000;
const height = 400;

const margins = {
  top: 10,
  bottom: 50,
  left: 40,
  right: 40,
};

const inner = {
  height: height - (margins.bottom + margins.top),
  width: width - (margins.left + margins.right),
};

const URL = [
  'static/data/data-area.csv',
  'static/data/data-grow.csv',
];

export default {
  name: 'Chart',
  mounted() {
    this.getData()
      .then(rawData => {
        const {
            areaData,
          growData,
          } = this.processData(rawData);

        const svg = d3.select(this.$el).append('svg').attrs({
          viewBox: `0 0 ${width} ${height}`,
        })
          .on('mousemove', handleMouseOver)
          .on('mouseout', handleMouseOut);

        const areaRange = d3.extent(areaData.map(d => d.area));
        const growRange = d3.extent(growData.map(d => d.total));
        const dateRange = d3.extent(([...areaData, ...growData]).map(d => d.date));

        const x = d3.scaleTime().domain(dateRange).range([0, inner.width]);
        const xAdjusted = d3.scaleTime().domain(areaData.map(d => d.date)).range(areaData.map(d => d.area));

        const xTicks = growData.map(d => x(d.date));

        const areaDataAdjusted = growData.map(d => ({
          date: d.date,
          area: xAdjusted(d.date),
        }));

        const y = d3
          .scaleLinear()
          .domain(areaRange)
          .range([inner.height, margins.top]);

        const yTotal = d3
          .scaleLinear()
          .domain(growRange)
          .range([inner.height, margins.top]);

        const defs = svg.append('defs');

        const gradientArea = defs
          .append('linearGradient')
          .attr('x1', '0')
          .attr('x2', '1')
          .attr('id', 'areaGradient');

        const gradientGrow = defs
          .append('linearGradient')
          .attr('x1', '0')
          .attr('x2', '1')
          .attr('id', 'growGradient');

        gradientArea
          .append('stop')
          .attr('class', 'area-stop-0')
          .attr('offset', '0.25');
        gradientArea
          .append('stop')
          .attr('class', 'area-stop-1')
          .attr('offset', '1');

        gradientGrow
          .append('stop')
          .attr('class', 'grow-stop-0')
          .attr('offset', '0.25');
        gradientGrow
          .append('stop')
          .attr('class', 'grow-stop-1')
          .attr('offset', '1');

        const xAxis = d3
          .axisBottom()
          .scale(x)
          .ticks(d3.timeYear.every(1))
          .tickFormat(d3.timeFormat('%Y'));

        svg
          .append('g')
          .attr('class', 'axis axis-x')
          .call(xAxis)
          .attr(
          'transform',
          `translate(${margins.left}, ${height - (margins.bottom - 5)})`,
        );

        const interpolation = d3.curveLinear; // d3.curveBasis;
        const triangle = d3.symbol()
          .type(d3.symbolTriangle)
          .size(30);

        const lineArea = d3.line().x(d => x(d.date)).y(d => y(d.area)).curve(interpolation);
        const areaArea = d3
          .area().curve(interpolation)
          .x(d => x(d.date))
          .y1(d => y(d.area) + 1)
          .y0(inner.height);

        const lineGrow = d3.line().x(d => x(d.date)).y(d => yTotal(d.total)).curve(interpolation);
        const areaGrow = d3
          .area().curve(interpolation)
          .x(d => x(d.date))
          .y1(d => yTotal(d.total) + 1)
          .y0(inner.height);

        const chart = svg
          .append('g')
          .attr('transform', `translate(${margins.left}, ${margins.top})`);

        chart
          .append('path')
          .datum(areaDataAdjusted)
          .attr('class', 'area')
          .attr('d', areaArea);

        chart
          .append('path')
          .datum(areaDataAdjusted)
          .attr('class', 'areaLine')
          .attr('d', lineArea);

        chart
          .append('path')
          .datum(growData)
          .attr('class', 'grow')
          .attr('d', areaGrow);

        chart
          .append('path')
          .datum(growData)
          .attr('class', 'growLine')
          .attr('d', lineGrow);


        const tooltip = chart.append('g')
          .classed('tooltip', true);

        tooltip.append('line');

        const tooltipHtml = d3.select(this.$el)
          .append('div')
          .classed('tooltip-html', true);

        const tooltipPopulation = tooltipHtml.append('p').classed('population', true).text('123');
        const tooltipArea = tooltipHtml.append('p').classed('area', true).text('123');

        chart
          .selectAll('.poointBase')
          .data(growData)
          .enter()
          .append('path')
          .classed('pointBase', true)
          .attrs({
            d: triangle,
            style: d => `transform: translate(${x(d.date)}px, ${yTotal(growRange[0])}px) scaleY(0.5);`,
          });

        chart
          .selectAll('.pointGrow')
          .data(growData)
          .enter()
          .append('circle')
          .attrs({
            class: 'pointGrow',
            r: POINT_RADIUS,
            cx: d => x(d.date),
            cy: d => yTotal(d.total),
          });

        chart
          .selectAll('.pointArea')
          .data(areaDataAdjusted)
          .enter()
          .append('circle')
          .attrs({
            class: 'pointArea',
            r: POINT_RADIUS,
            cx: d => x(d.date),
            cy: d => y(d.area),
          });

        function handleMouseOut() {
          tooltip.classed('visible', false);
          tooltipHtml.classed('visible', false);
        }

        function handleMouseOver() {
          const svgBounds = svg.node().getBoundingClientRect();

          const matrix = svg.node().getScreenCTM();
          const p = svg.node().createSVGPoint();
          p.x = d3.event.pageX;
          p.y = d3.event.pageY;
          const pSvg = p.matrixTransform(matrix.inverse());
          pSvg.x -= margins.left;

          let xDiff = 1e99;
          let xPos = 0;
          let n = 0;
          for (let i = 0; i < xTicks.length; i++) {
            const diff = Math.abs(pSvg.x - (xTicks[i]));
            if (diff < xDiff) {
              n = i;
              xDiff = diff;
              xPos = xTicks[i];
            }
          }

          const yPos = Math.min(yTotal(growData[n].total), y(areaDataAdjusted[n].area));
          p.x = xPos + margins.left;
          p.y = yPos;

          const xPosReal = p.matrixTransform(matrix);
          xPosReal.x = xPosReal.x + 60 > svgBounds.width ? xPosReal.x - 60 : xPosReal.x;
          tooltip.classed('visible', true);
          tooltipHtml.classed('visible', true);

          tooltip.select('line')
            .attrs({
              x1: xPos,
              y1: yTotal(growRange[0]),
              x2: xPos,
              y2: yPos,
            });

          tooltipHtml
            .transition()
            .duration(100)
            .style('left', `${xPosReal.x - 60}px`)
            .style('top', `${xPosReal.y - 100}px`);

          tooltipPopulation.text(`${growData[n].total} os.`);
          tooltipArea.text(`${areaData[n].area} mkw.`);
        }
      });
  },
  methods: {
    getData() {
      return Promise.all(URL.map(url => fetch(url).then(r => r.text()).then(d => d3.csvParse(d))));
    },
    processData(rawData) {
      const areaData = rawData[0].map(d => ({
        date: moment(d.data, 'DD.MM.YYYY').toDate(),
        area: parseInt(d.area, 10),
      }));

      const growData = rawData[1].map(d => ({
        date: moment(d.data, 'DD.MM.YYYY').toDate(),
        total: parseInt(d.total, 10),
      }));

      return {
        areaData,
        growData,
      };
    },
  },
};

</script>

<style lang="scss">
@import 'base';

$color-area: #FF5A4F;
$color-grow: #FF7626;

.chartContainer {
  width: 90%;
  max-width: $maxWidth;
  padding: 0;
  margin-bottom: 100px;
  align-self: center;

  @include for-size(big-desktop-up) {
    width: 100%;
  }

  svg {
    position: relative;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
  }

  .axis-x {
    text {
      font-size: 24px;
      font-weight: 100;
      font-family: 'FF Mark light', sans-serif;
      fill: rgba(236, 240, 241, 1.0);
      @include for-size(laptop-up) {
        font-size: 12px;
      }
    }
    line,
    path {
      display: none;
      stroke: rgba(236, 240, 241, 1.0);
    }
  }

  .axis-area {
    text {
      fill: $color-area;
    }
    path,
    line {
      stroke: $color-area;
      stroke-width: 2;
    }
  }

  .grid-area {
    .domain,
    text {
      display: none;
    }
    .tick line {
      transform: translateX(10px);
      stroke: rgba(#fff, 0.25);
      stroke-dasharray: 5 5;
    }
  }

  .axis-grow {
    text {
      fill: darken($color-grow, 20%);
    }
    path,
    line {
      stroke: lighten($color-grow, 10%);
      stroke-width: 2;
    }
  }

  path.areaLine {
    fill: none;
    stroke: adjust-hue($color-area, -10deg);
    stroke-width: 1px;
  }

  path.area {
    fill: none;
    stroke: none;
    fill: url(#areaGradient);
  }

  .area-stop-0 {
    stop-color: rgba($color-area, 0.4);
  }

  .area-stop-1 {
    stop-color: rgba($color-area, 0.1);
  }

  path.growLine {
    fill: none;
    stroke: adjust-hue($color-grow, 10deg);
    stroke-width: 1px;
  }

  path.grow {
    fill: none;
    stroke: none;
    fill: url(#growGradient);
  }

  .grow-stop-0 {
    stop-color: rgba($color-grow, 0.4);
  }

  .grow-stop-1 {
    stop-color: rgba($color-grow, 0.1);
  }

  .pointGrow {
    fill: saturate(lighten(adjust-hue($color-grow, 20deg), 0%), 0%);
    stroke: adjust-hue($color-grow, 10deg);
  }

  .pointArea {
    fill: saturate(lighten(adjust-hue($color-area, 20deg), 0%), 0%);
    stroke: adjust-hue($color-area, 10deg);
  }

  .pointBase {
    fill: $color-black-pearl;
    transform-origin: 50% 50%; // transform: scaleY(0.5);
  }

  .tooltip {
    opacity: 0;
    transition: opacity .5s cubic-bezier(.645, .045, .355, 1);
  }

  .tooltip.visible {
    opacity: 1;
  }

  .tooltip line {
    stroke: rgba(#fff, 0.25);
    stroke-width: 2;
    stroke-dasharray: 5;
  }

  .tooltip-html {
    opacity: 0;
    position: absolute;
    width: 120px;
    top: 200px;
    left: 200px;
    border: 1px solid $color-area;
    border-radius: 10px;
    background-color: rgba($color-black-pearl, 0.5);
    font-size: 16px;
    padding: 10px;
    transition: opacity .5s cubic-bezier(.645, .045, .355, 1);
    pointer-events: none;

    p {
      line-height: $icon-size;
      margin: 0;
    }

    p.population:before,
    p.area:before {
      display: inline-block;
      content: ' ';
      background-size: $icon-size $icon-size;
      height: $icon-size;
      width: $icon-size;
      margin-right: 10px;
      vertical-align: middle;
    }
    p.population:before {
      background-image: url('/static/icon_people.svg');
    }
    p.area:before {
      background-image: url('/static/icon_space.svg');
    }
  }

  .tooltip-html.visible {
    opacity: 1;
  }
}
</style>



