<script>
import {
  isMobilePortrait,
} from '../common/utils';

export default {
  data() {
    return {
      isMobilePortrait: isMobilePortrait(),
    };
  },
  name: 'IntroGraphic',
};

</script>

<style lang="scss" scoped>
@import 'base';

$animation-time: 2000ms;
$stroke-length: 900;

svg {
  height: 90%;
  width: 90%;
  @include for-size(laptop-up) {
    height: 100%;
    max-height: 280px;
  }
}

@mixin animation-base($name) {
  animation-name: $name;
  animation-delay: 500ms;
  animation-duration: $animation-time;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
  animation-timing-function: ease-in;
}

path,
line,
circle,
polyline,
rect {
  stroke-dasharray: $stroke-length;
  @include animation-base(svgDraw);
}

.arrow,
.textWroclavia {
  @include animation-base(showEnd);
}

.truck {
  @include animation-base(showHalfMove);
}


@keyframes showHalfMove {
  0% {
    opacity: 0;
    transform: translate(-350px, 0) skewX(20deg);
  }
  33% {
    opacity: 1;
    transform: translate(5px, 0) skewX(5deg);
  }
  35% {
    opacity: 1;
    transform: skewX(-2deg);
  }
  36% {
    opacity: 1;
    transform: translate(0px, 0) skewX(0);
  }
}

@keyframes showEnd {
  0% {
    opacity: 0;
  }
  85% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
}

@keyframes svgDraw {
  0% {
    // stroke-dasharray: 5 500;
    stroke-dashoffset: $stroke-length;
  }
  100% {
    // stroke-dasharray: 5 0;
    stroke-dashoffset: 0;
  }
}
</style>



