<template>
  <div class="Infographic" :class="[{'Infographic--active': isActive}, 'Infographic--slide-' + animationStep]">
    <div class="Infographic-slides">
        <div class="Infogragic-slides--partial">
            <h1>Lubisz MPK? Dojedziesz do nas aż 28 różnymi połączeniami!</h1>
            <h3>Tarnogaj, Park Południowy, Marino, Stadion Wrocław, Gaj, czy Kozanów… to tylko niektóre kierunki, z których dojedziesz do Wroclavii tramwajem. Trafisz do nas aż 13 różnymi liniami tramwajowymi i 15 autobusowymi.</h3>
            <h3>MPK to za mało? W niecałe 5 min dojdziesz do nas z Dworca Głównego PKP, a PKS znajdziesz jeszcze bliżej! Nie musisz mieszkać we Wrocławiu, aby bez problemu się do nas dostać.</h3>
        </div>
        <div class="Infogragic-slides--partial">
            <h1>Jesteś miłośnikiem jazdy na dwóch kółkach? Wroclavia to miejsce dla Ciebie! </h1>
            <h3>1 300 metrów nowych ścieżek rowerowych, 400 rowerowych miejsc parkingowych i przynajmniej 3 stacje rowerowe w bliskim sąsiedztwie budynku. Z tego wszystkiego mogą skorzystać nasi entuzjaści jazdy rowerowej.</h3>
            <h3>I na tym nie koniec! PGS Software popiera przesiadkę na rower. Organizujemy rowerowe wycieczki i bierzemy udział w rowerowych zawodach.</h3>
        </div>
        <div class="Infogragic-slides--partial">
            <h1>Piechotą też można!</h1>
            <h3>Siedziba w samym centrum daje Ci łatwy dostęp do kluczowych punktów na mapie Wrocławia.</h3>
            <h3>
                <ul>
                    <li>Dworzec Główny? 2 minuty drogi!</li>
                    <li>Rynek? 15-minutowy spacerek.</li>
                    <li>Arkady Capitol? Mniej, niż 10 minut.</li>
                </ul>
            </h3>
            <h3>To co, w drogę? :) </h3>
        </div>
    </div>

